/*--------------- Preset -----------------*/
a {
  text-decoration: none;
  color: inherit;
  -webkit-transition: all 0.25s;
  -o-transition: all 0.25s;
  transition: all 0.25s;
  color: #ddd;
}
a:hover {
  color: #bbb;
  text-decoration: none;
}
.card {
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
h2 {
  margin: 0;
}
ul {
  padding: 0;
}
.font-48 {
  font-size: 48px;
}
/*--------------- Preset -----------------*/
/*----------------- App ------------------*/
.App {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: white;
  background: #eee;
  overflow-y: scroll;
}
.App .mainContent {
  margin-top: 65px;
  width: 100%;
}
/*----------------- App ------------------*/



